<template>
  <div id="app">
    <LandingPage/>
  </div>
</template>

<script>
import LandingPage from './components/LandingPage.vue';
import axios from 'axios';
import Vue from 'vue';
import VueMeta from 'vue-meta';
Vue.use(VueMeta);

export default {
  name: 'App',
  components: {
    LandingPage,
  },

  data() {
    return {
      metaTitle: '',
      metaImage: '',
      metaUrl: '',
    };
  },

  metaInfo() {
    return {
      title: this.metaTitle,
      meta: [
        { property: 'og:title', content: this.metaTitle },
        { property: 'og:image', content: this.metaImage },
        { property: 'og:url', content: this.metaUrl },
        { property: 'twitter:title', content: this.metaTitle },
        { property: 'twitter:image', content: this.metaImage },
        { property: 'twitter:url', content: this.metaUrl },
        { name: 'medium:title', content: this.metaTitle },
        { name: 'medium:image', content: this.metaImage },
        { name: 'medium:link', content: this.metaUrl },
        { name: 'linkedin:title', content: this.metaTitle },
        { name: 'linkedin:image', content: this.metaImage },
        { name: 'linkedin:url', content: this.metaUrl },
      ],
    };
  },

  mounted() {
    axios
      .get('https://manage.kolibriforimpact.com/api/contents/1?populate=*')
      .then(response => {
        const data = response.data.data.attributes;

        // Set the meta data properties
        this.metaTitle = data.title;
        this.metaImage = data.image.data.attributes.formats.large.url;
        this.metaUrl = window.location.href;

        // const imageBase = 'https://manage.kolibriforimpact.com';
        // const imageFormats = data.image.data.attributes.formats;

        // for (const format in imageFormats) {
        //   const url = imageBase + imageFormats[format].url;
        //   this.metaImage[format] = url;
        // }

      })
      .catch(error => {
        console.log(error);
      });
  },
}
</script>

