<template>
  <div class="body flex-column">
    <img src="../assets/image/background-image.png" alt="" class="background">

    <div class="div-segment-1 flex-column">
      <h1 class="font-title">{{ bodyArrayTitle[0].toUpperCase() }}: </h1>

      <div class="segment-1-sub flex-column">
        <h3 class="font-subtitle">{{ bodyArrayTitle[1] }}</h3>
        <div class="red-rectangle"></div>
      </div>
    </div>

    <div class="div-segment-2"></div>

    <div class="div-234px">      
      <div class="div-segment-3 flex-row">
        <div class="text-segment flex-column">
          <div class="text-header-image"></div>
          <p class="font-regular">{{ bodyArray[0] }}</p>
        </div>

        <div class="image-segment-3"></div>
      </div>

      <div class="div-segment-4 flex-row">
        <div class="image-segment-4"></div>
        <div class="text-segment-1 flex-column">
          <p class="font-regular">{{ bodyArray[1] }}</p>
          <div class="filler"></div>
        </div>
        <div class="text-segment-2 flex-column">
          <div class="filler"></div>
          <div class="text-image flex-column">
            <div class="image"></div>
            <p class="font-regular">{{ bodyArray[2] }}</p>
          </div>
        </div>
      </div>

      <div class="div-segment-5 flex-column">
        <div class="black-line"></div>
        <p class="font-regular">{{ bodyArray[3] }}</p>
        <div class="black-line"></div>
      </div>

      <div class="div-segment-6 flex-row">
        <div class="item-1 flex-column-bottom">
          <div class="text-border">
            <p>{{ bodyArray[5].slice(2) }}</p>
          </div>       
        </div>
        <div class="item-2 flex-column-bottom">
          <div class="text-border">
            <p>{{ bodyArray[4].slice(2) }}</p>
          </div>
        </div>
        <div class="item-3 flex-column-bottom">
          <div class="text-border">
            <p>{{ bodyArray[6].slice(2) }}</p>
          </div>
