      <div class="div-segment-7 black-line"></div>
      
      <div class="div-segment-8 flex-column">
        <h3>Further Issues at the Center of Protests</h3>
        <div class="text-content flex-row">
          <div class="filler"></div>
          <p class="font-regular">{{ bodyArray[8] }}</p>
          <p class="font-regular">{{ bodyArray[9] }}</p>
        </div>
      </div>

      <div class="div-segment-9"></div>
    </div>

  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'LandingPage',
  
  data() {
    return {
      createdAt: '',
      updatedAt: '',
      publishedAt: '',
      bodyArray: [],
      bodyArrayTitle: [],
    };
  },

  mounted() {
    axios
      .get('https://manage.kolibriforimpact.com/api/contents/1?populate=*')
      .then(response => {
        const data = response.data.data.attributes;
        this.bodyArray = data.body.split('\n');
        this.bodyArrayTitle = data.title.split(': ');
        this.createdAt = data.createdAt;
        this.updatedAt = data.updatedAt;
        this.publishedAt = data.publishedAt;
      })
      .catch(error => {
        console.log(error);
      });
  },
};
</script>
